'use client'

import { useState } from 'react'
import Link from 'next/link'
import { ShoppingCart, Menu, User, LogOut, X, Search } from 'lucide-react'
import { useAuth } from '@/context/AuthContext'
import { useCart } from '@/context/CartContext'
import AuthModal from '@/components/auth/AuthModal'

const categories = ['Oshxona', 'Tozalash', 'Isitish', 'Parvarish', 'Aqlli uy']

export default function Navbar() {
	const { isLoggedIn, user, logout } = useAuth()
	const { totalCartCount } = useCart()
	const [isAuthModalOpen, setAuthModalOpen] = useState(false)
	const [isMobileMenuOpen, setIsMobileMenuOpen] = useState(false)

	return (
		<>
			<AuthModal
				isOpen={isAuthModalOpen}
				onClose={() => setAuthModalOpen(false)}
			/>

			<header className='sticky top-0 z-50 w-full border-b border-border bg-background'>
				<div className='container mx-auto px-4'>
					<div className='h-16 flex items-center justify-between'>
						<Link
							href='/'
							className='text-xl font-light tracking-wide text-foreground'
						>
							Tech<span className='font-semibold'>House</span>
						</Link>

						{/* Desktop Navigation */}
						<div className='hidden lg:flex items-center gap-12 flex-1 mx-10'>
							<div className='flex gap-8'>
								{categories.map(cat => (
									<button
										key={cat}
										className='text-sm text-muted-foreground hover:text-foreground transition-smooth'
									>
										{cat}
									</button>
								))}
							</div>
						</div>

						{/* Actions */}
						<div className='hidden md:flex items-center gap-6'>
							<div className='relative flex items-center'>
								<input
									type='text'
									placeholder='Qidirish...'
									className='px-4 py-2 text-sm border border-border rounded-lg bg-background text-foreground placeholder-muted-foreground focus:outline-none focus:ring-2 focus:ring-primary/50 transition-smooth w-48'
								/>
								<Search className='absolute right-3 h-4 w-4 text-muted-foreground' />
							</div>

							<Link
								href='/cart'
								className='relative flex items-center text-muted-foreground hover:text-foreground transition-smooth'
							>
								<ShoppingCart className='h-5 w-5' />
								{totalCartCount > 0 && (
									<span className='absolute -top-2 -right-2 bg-accent text-accent-foreground text-xs font-bold px-1.5 py-0.5 rounded-full'>
										{totalCartCount}
									</span>
								)}
							</Link>

							{isLoggedIn ? (
								<div className='flex items-center gap-4 pl-4 border-l border-border'>
									<span className='text-sm text-muted-foreground'>
										{user?.phone}
									</span>
									<button
										onClick={logout}
										className='flex items-center gap-2 text-destructive hover:text-destructive/80 text-sm transition-smooth'
									>
										<LogOut className='h-4 w-4' />
									</button>
								</div>
							) : (
								<button
									onClick={() => setAuthModalOpen(true)}
									className='flex items-center gap-2 px-4 py-2 bg-primary hover:bg-primary/90 text-primary-foreground rounded-lg transition-smooth text-sm font-medium'
								>
									<User className='h-4 w-4' />
									Kirish
								</button>
							)}
						</div>

						{/* Mobile Menu Button */}
						<button
							className='md:hidden text-muted-foreground'
							onClick={() => setIsMobileMenuOpen(!isMobileMenuOpen)}
						>
							{isMobileMenuOpen ? (
								<X className='h-6 w-6' />
							) : (
								<Menu className='h-6 w-6' />
							)}
						</button>
					</div>

					{/* Mobile Menu */}
					{isMobileMenuOpen && (
						<div className='md:hidden border-t border-border py-4 space-y-3 bg-card'>
							<div className='flex flex-col gap-0'>
								{categories.map(cat => (
									<button
										key={cat}
										className='text-left px-4 py-2.5 hover:bg-muted text-sm text-foreground transition-smooth'
									>
										{cat}
									</button>
								))}
							</div>
							<div className='border-t border-border pt-4'>
								<Link
									href='/cart'
									className='flex items-center gap-2 px-4 py-2 text-foreground'
								>
									<ShoppingCart className='h-5 w-5' />
									Savat {totalCartCount > 0 && `(${totalCartCount})`}
								</Link>
							</div>
							{!isLoggedIn && (
								<button
									onClick={() => {
										setAuthModalOpen(true)
										setIsMobileMenuOpen(false)
									}}
									className='w-full px-4 py-2 bg-primary text-primary-foreground rounded-lg text-sm font-medium transition-smooth'
								>
									Kirish
								</button>
							)}
						</div>
					)}
				</div>
			</header>
		</>
	)
}
